.single-event {
  padding: 10px 10px 4px 10px;
  background-color: #ffffff;
  margin-top: 20px;
  border-radius: 5px;
  border: 1px solid gray;
  display: flex;
  align-items: center;
  justify-content: space-between;
  img {
    height: 100px;
    width: 500px;
    border-radius: 0 5px 5px 0;
    object-fit: cover;
  }
}
