* {
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
  .register {
    height: 100vh;
    background-color: #0fbffa;
    display: flex;
    align-items: center;
    justify-content: center;
    .card {
      background-color: azure;
      padding: 20px;
      border-radius: 10px;
      width: 350px;
      box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
        rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
      h1 {
        color: #0fbffa;
        text-align: center;
        margin: 25px;
      }
      form {
        display: flex;
        flex-direction: column;
        align-items: center;
        input {
          width: 80%;
          padding: 10px;
          margin: 15px 0;
          border: solid 1px #0fbffa;
          border-radius: 3px;
        }
        button {
          background-color: #0fbffa;
          color: white;
          font-size: 20px;
          border: none;
          padding: 10px;
          margin: 20px;
          width: 50%;
          border-radius: 3px;
          cursor: pointer;
        }
      }
      span {
        color: grey;
        font-size: 12px;
        text-align: center;

        p {
          margin-bottom: 25px;
          span {
            color: red;
            cursor: pointer;
          }
        }
      }
    }
  }
}
