.single-main {
  max-width: 1035px;
  margin: auto;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
    rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
  .single {
    padding: 25px 10px;
    .event-description {
      .creator-bar {
        display: flex;
        align-items: center;

        gap: 15px;
        .event-creator {
          display: flex;
          align-items: center;
          justify-content: center;
          gap: 5px;
          background-color: #e2e2e2;
          max-width: fit-content;
          padding: 5px 15px;
          border-radius: 50px;
        }
        .message-icon {
          background-color: #e2e2e2;
          max-width: fit-content;
          padding: 5px 15px 0 15px;
          border-radius: 50px;
        }
      }
      h1 {
        color: #2400ff;
        font-weight: 800;
        font-size: 50px;
        padding: 7px 0;
      }
      h2 {
        background-color: #2400ff;
        padding: 10px 25px;
        color: white;
      }
      h3 {
        padding: 10px 25px;
        border: 1px solid gray;
        max-width: fit-content;
        margin: 10px 0;
      }
    }

    img {
      height: 500px;
      width: 100%;
      object-fit: cover;
    }
    .event-desc {
      margin: 20px 0;
      display: flex;
      align-items: flex-start;
      gap: 20px;
      p {
        width: 700px;
      }
      button {
        width: 300px;
        height: fit-content;
        color: white;
        background-color: green;
        border: none;
        padding: 10px;
        font-size: 20px;
        border-radius: 50px;
      }
    }
  }
}
