.eventcreate-main {
  width: 1035px;
  margin: auto;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
    rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
  .event-creation {
    padding: 25px 10px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    h1 {
      font-size: 45px;
      margin-bottom: 15px;
    }
    .event-name {
      input {
        padding: 5px;
        font-size: 30px;
        width: 500px;
        margin: 10px 0;
      }
    }
    .event-location {
      input {
        padding: 5px;
        font-size: 20px;
        width: 500px;
        margin: 10px 0;
      }
    }
    .event-datetime {
      input {
        padding: 5px;
        font-size: 20px;
        width: 500px;
        margin: 10px 0;
      }
    }
    .event-banner {
      input {
        padding: 5px;
        font-size: 20px;
        width: 500px;
        margin: 10px 0;
      }
    }
    .event-description {
      textarea {
        padding: 5px;
        width: 500px;
        margin: 10px 0;
      }
    }
    button {
      width: fit-content;
      padding: 10px 25px;
      font-size: 20px;
      background-color: green;
      color: white;
      border: none;
      border-radius: 5px;
      margin: 15px 0;
    }
  }
}
