.home {
  max-width: 1035px;
  background-color: #f1f1f1;
  margin: auto;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
    rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
  .container {
    padding: 20px;
    .events-in-your-area {
      background-color: rgb(197, 197, 197);
      padding: 50px;
      border-radius: 5px;
      background: url(https://media.istockphoto.com/id/1031437062/photo/people-at-a-party.jpg?s=612x612&w=0&k=20&c=VP1Xty79_vuNWd61lvDU6Ax09PGXtvxHJOmx-DCI5Kc=)
        repeat;
      background-size: auto;
      h1 {
        text-align: center;
        color: white;
        font-size: 50px;
      }
    }
    .create-events {
      background-color: rgb(197, 197, 197);
      padding: 50px;
      border-radius: 5px;
      margin-top: 10px;
      background: url(https://media.istockphoto.com/id/501387734/photo/dancing-friends.jpg?s=612x612&w=0&k=20&c=SoTKXXMiJYnc4luzJz3gIdfup3MI8ZlROFNXRBruc10=)
        repeat;
      background-size: auto;
      h1 {
        text-align: center;
        color: white;
        font-size: 50px;
      }
    }
    .event-list {
      h1 {
        // border-top: 2px solid grey;
        border-bottom: 2px solid grey;
        text-align: center;
        margin-top: 20px;
        padding: 10px;
      }
    }
  }
}
