.profile-main {
  background-color: rgb(255, 255, 255);
  max-width: 1035px;
  margin: auto;

  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
    rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
  .profile-details {
    padding: 20px;
    display: flex;
    align-items: center;
    gap: 20px;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
      rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
    img {
      height: 250px;
      width: 250px;
      object-fit: cover;
      border-radius: 50%;
    }
    .profile-description {
      color: black;
      height: 200px;
      background-color: aliceblue;
      padding: 10px;
      border: 1px solid gray;
      display: flex;
      flex-direction: column;
      gap: 20px;
    }
  }
  .profile-events {
    display: flex;
    .upcoming-events {
      h1 {
        background-color: #76e889;
        box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
          rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
        color: white;
        padding: 10px 10px;
      }
      padding: 10px;
      .profile-single-event {
        border: 1px solid gray;
        margin: 10px 0;
        img {
          height: 150px;
          width: 497px;
          object-fit: cover;
        }
        .p-event-description {
          display: flex;
          align-items: flex-start;
          justify-content: space-between;
          padding: 10px;
        }
      }
    }
    .past-events {
      padding: 10px;
      h1 {
        background-color: #ec2e5f;
        box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
          rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
        color: white;
        padding: 10px 10px;
      }
      .profile-single-event {
        border: 1px solid gray;
        margin: 10px 0;
        img {
          height: 150px;
          width: 496px;
          object-fit: cover;
        }
        .p-event-description {
          display: flex;
          align-items: flex-start;
          justify-content: space-between;
          padding: 10px;
        }
      }
    }
  }
}
