* {
  padding: 0;
  margin: 0;
  text-decoration: none;
  .navbar {
    max-width: 1000px;
    position: sticky;
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    padding-right: 25px;
    background-color: #f1f1f1;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
      rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;

    .logo {
      display: flex;
      align-items: center;
      img {
        height: 50px;
      }

      span {
        font-size: 25px;
        text-decoration: none;
        color: #2400ff;
        font-weight: 700;
        margin-left: 10px;
      }
    }
    .profile {
      display: flex;
      align-items: center;
      gap: 5px;
    }
  }
}
